// Small tablets and large smartphones (landscape view)
// My tnh bng nh v in thoi thng minh ln (ch  xem ngang)
$screen-sm-min: 576px;

// Small tablets (portrait view)
// My tnh bng nh (ch  xem dc)
$screen-md-min: 768px;

// Tablets and small desktops
// My tnh bng v my tnh  bn nh
$screen-lg-min: 992px;

// Large tablets and desktops
// My tnh bng v my tnh  bn ln
$screen-xl-min: 1200px;

// Small devices
@mixin sm {
    @media (min-width: #{$screen-sm-min}) {
        @content;
    }
}

// Medium devices
@mixin md {
    @media (min-width: #{$screen-md-min}) {
        @content;
    }
}

// Large devices
@mixin lg {
    @media (min-width: #{$screen-lg-min}) {
        @content;
    }
}

// Extra large devices
@mixin xl {
    @media (min-width: #{$screen-xl-min}) {
        @content;
    }
}
// Custom devices
@mixin rwd($screen) {
    @media (min-width: $screen) {
        @content;
    }
}
